import { valuationLabels, bitcoinPredictLabels } from './../constants'
import { formatUSD } from './../../../services/moneyService'

export const coinMapper = (coin) => {
  if (!coin.rating) {
    coin.rating = {
      priceTarget: 0,
      buy: 0,
      sell: 0,
      hodl: 0,
      rating: 'NONE',
    }
  }
  return {
    currentPrice: formatUSD(coin.price),
    target: formatUSD(coin.rating.priceTarget),
    rank: coin.totalVolumeSortOrder,
    coinName: coin.fullName.substring(0, coin.fullName.indexOf('(') - 1),
    image: coin.imageUrl,
    buy: coin.rating.buy,
    sell: coin.rating.sell,
    hodl: coin.rating.hodl,
    rating: coin.rating.rating,
    projection: _getForecastValue(coin.rating.priceTarget, coin.price),
  }
}

export const confidenceMapper = (confidence) => ({
  buy: confidence.buy.toFixed(0),
  sell: confidence.sell.toFixed(0),
  hodl: confidence.hodl.toFixed(0),
})

const _getForecastValue = (targetPrice, currentPrice) => {
  const forecastValue = (targetPrice / currentPrice - 1) * 100
  const finalValue = isFinite(forecastValue) ? forecastValue : 0
  return {
    forecast: finalValue.toFixed(0),
    absoluteForecast: Math.abs(finalValue).toFixed(0),
  }
}

export const valuationMapper = (valuation) =>
  Object.keys(valuation).map((key) => ({
    label: valuationLabels[key],
    value: valuation[key],
  }))

export const bitcoinPredictMapper = (btcPredict) =>
  Object.keys(btcPredict)
    .map((key) => ({
      label: bitcoinPredictLabels[key],
      value1: btcPredict[key][0],
      value2: btcPredict[key][1],
    }))
    .filter(({ label }) => label !== undefined)
    .reverse()
